import Calendar from "component/Calendar";
import dayjs, { Dayjs } from "dayjs";
import { useState } from "react";
const today = dayjs();
const defaultDisableDate = (date: Dayjs): boolean => date.isBefore(today, 'day');
const CalendarBaseView = () => {

    const [value, setValue] = useState();
    const dateCellRender = (t: any) => {
        if (today.isSame(t, 'day')) {
            return (
                <div>渲染今天</div>
            )
        }
        return <></>
    }
    return (
        <Calendar onChange={setValue} dateCellRender={dateCellRender} disableDate={defaultDisableDate} />
    )
}

export default CalendarBaseView;