
// import { Button } from 'antd';
import React, { useMemo, useState } from 'react';
// import { IconDropLeft, IconDropRight } from '@workec/ec-icon';
import dayjs, { Dayjs } from 'dayjs';
import arraySupport from 'dayjs/plugin/arraySupport';
import classNames from 'classnames';
import styles from './index.less';
import useCurrentYearMonth from './useCurrentYearMonth';
import Button from 'component/Button';

dayjs.extend(arraySupport);


const today = dayjs();
const zeroDate = dayjs(today.format('YYYY-MM-DD'));
const defaultDisableDate = (date: Dayjs): boolean => false;
interface Props {
    onChange: (time: any, isCurrentMonth?: boolean) => void;
    dateCellRender?: (time: any) => JSX.Element;
    disableDate?: (t: any) => boolean;
}
const Calendar = (props: Props): JSX.Element => {
    const { disableDate = defaultDisableDate, onChange, dateCellRender } = props;
    const [year, month, setYear, setMonth] = useCurrentYearMonth();
    const [value, setValue] = useState<Dayjs>(zeroDate);
    // zc
    const data = useMemo(() => {
        const thisMonth = dayjs([year, month]);
        const weekDay = thisMonth.day() ? thisMonth.day() - 1 : thisMonth.day() + 6;
        const list = [] as any;
        let addDay = 1;

        const makeData = (date: Dayjs, key: string) => ({
            value: date,
            text: date.format('DD'),
            key,
            isCurrentMonth: date.isSame(thisMonth, 'month') === false,
            disable: disableDate(date),
        });

        for (let i = 0; i < 6; i += 1) {
            const thisWeek = [] as any;
            for (let weekend = 0; weekend < 7; weekend += 1) {
                const key = `${thisMonth.month()}_${i}_${weekend}`;
                if (i === 0) {
                    if (weekend <= weekDay) {
                        const date = thisMonth.subtract(weekDay - weekend, 'day');
                        thisWeek.push(makeData(date, key));
                        continue;
                    }
                }

                const date = thisMonth.add(addDay, 'day');
                thisWeek.push(makeData(date, key));
                addDay += 1;
            }
            list.push({
                key: `${thisMonth.month()}_${i}`,
                cells: thisWeek,
            });
        }

        return {
            list,
            year: `${thisMonth.format('YYYY')}年`,
            month: `${thisMonth.format('MM')}月`,
        };
    }, [disableDate, month, year]);

    console.log(disableDate);


    const handleToday = (): void => {
        const y = today.year();
        const m = today.month();
        setYear(y);
        setMonth(m);
        setValue(zeroDate);
        onChange(zeroDate);
    };
    const handleToggleMonth = (type: string): void => {
        setMonth((old: number) => {
            if (type === 'next') {
                return old + 1;
            }

            return old - 1;
        });
    };

    const handleSelect = (item: any): void => {
        setYear(item.value.format('YYYY'));
        setMonth(item.value.format('MM') - 1);
        setValue(item.value);
        // 点击灰色区域
        onChange(item.value, item.isCurrentMonth);
    };


    return (
        <div className={styles.container}>
            <div className={styles.header}>
                <div className={styles.text}>
                    <span style={{ cursor: 'pointer' }} onClick={(): void => handleToggleMonth('prev')}>
                        《
                        {/* <IconDropLeft
                            width={14}
                            height={14}
                            className={styles.dropIcon}

                        /> */}
                    </span>

                    <span className={styles.yearText}>
                        {data.year}
                        {data.month}
                    </span>
                    <span style={{ cursor: 'pointer' }} onClick={(): void => handleToggleMonth('next')}>
                        {/* <IconDropRight
                            width={14}
                            height={14}
                            className={styles.dropIcon}
                        /> */}
                        》
                    </span>

                </div>
                <Button className={styles.btn} onClick={handleToday}>今天</Button>
            </div>
            <div className={styles.dayRow}>
                <span className={styles.item}>一</span>
                <span className={styles.item}>二</span>
                <span className={styles.item}>三</span>
                <span className={styles.item}>四</span>
                <span className={styles.item}>五</span>
                <span className={styles.item}>六</span>
                <span className={styles.item}>日</span>
            </div>
            {/* {data} */}
            {
                data.list.map((item: any) => (
                    <div key={item.key} className={styles.row}>
                        {
                            item.cells.map((sub: any) => (
                                <span
                                    key={sub.key}
                                    className={
                                        classNames(
                                            styles.item, {
                                            [styles.disable]: sub.disable,
                                            [styles.noCurrentMonth]: sub.isCurrentMonth,
                                            [styles.active]: sub.value.isSame(value, today) && !sub.isCurrentMonth,
                                        },

                                        )
                                    }
                                    onClick={(): void => {
                                        handleSelect(sub);
                                    }}
                                >
                                    {
                                        sub.value.isSame(today, 'day') && !sub.isCurrentMonth ? (
                                            <span>
                                                <span style={{ color: '#2F85F9' }}>
                                                    {sub.text}
                                                    <span>今天</span>
                                                </span>
                                            </span>
                                        ) : <span>{sub.text}</span>
                                    }

                                    {/* // 显示外面传入的 */}
                                    {
                                        dateCellRender ? dateCellRender(sub.value) : null
                                    }

                                </span>
                            ))
                        }
                    </div>
                ))
            }
        </div>
    );
};

export default Calendar;
